import * as React from 'react'
import Input from 'arui-feather/input'
import './styles.sass'

class Header extends React.Component {

  handleChange = ev => {
    console.log(ev)
  }

  render() {
    return  (
      <header className="header">
        <Input
          placeholder='Search'
          view='line'
          size='l'
          onChange={this.handleChange}
        />
      </header>
    ) 
  }
}

export default Header